}

@font-face {
  font-family: 'georgia';
  src: url("../fonts/georgia.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: 'georgia';
  src: url("../fonts/georgia-italic.ttf") format("truetype");
  font-style: italic;
  font-weight: 400;
  font-display: swap;
}

@font-face {
  font-family: 'BarlowCondensed';
  src: url("../fonts/BarlowCondensed-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

body {
  background-image: url(../img/bg.jpg);
  padding-bottom: 170px;
  font-family: "georgia", sans-serif;
}

body:before {
  content: "";
  height: 600px;
  width: 100%;
  background: url(../img/clouds2.png);
  background-position: center;
  position: absolute;
  top: 150px;
  z-index: -1;
}

.container {
  width: 100%;
  min-width: 320px;
  max-width: 1000px;
  margin: 0 auto;
  z-index: -500000;
}

.main-content {
  background-color: #EDEDED;
  padding: 30px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
}

.responsive-img {
  width: 100%;
  height: auto;
}

.gradient {
  width: 100%;
  z-index: -2;
  height: 240px;
  position: absolute;
  top: 0;
  background: linear-gradient(to bottom, #292826 0%, #23346a 20%, #2a5aac 40%, #3e8ad6 60%, #9cd1f0 80%, #c8d8e5 99%, #c7d7e4 100%);
}

.logo-bottom {
  display: inline-block;
  margin-top: 280px;
}

@media (max-width: 960px) {
  .main-content {
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .main-content {
    padding: 30px 0;
  }
  .logo-bottom {
    display: block;
    text-align: center;
    margin-top: 30px;
  }
  body {
    padding-bottom: 50px;
  }
}

.top-new {
  margin-top: 35px;
  min-height: 40px;
  font-size: 11px;
  color: #757575;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  background: linear-gradient(to bottom, white 0%, #f6f6f6 47%, #ededed 100%);
  border-radius: 4px 4px 0 0;
  padding-left: 30px;
  font-style: italic;
}

.top-new__hottest {
  background-color: #85AFBD;
  border-radius: 4px;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
  padding: 4px 5px;
  margin-right: 15px;
  font-family: "BarlowCondensed";
  font-style: normal;
}

.top-new__full {
  display: inline-block;
  margin-left: 10px;
  width: 10px;
  height: 10px;
  background: url(../img/arrow.png) no-repeat;
}

@media (max-width: 960px) {
  .top-new {
    margin-top: 0;
  }
}

.main-header {
  position: relative;
  min-height: 130px;
  background-image: url(../img/flag.png);
  background-color: #07313F;
  padding: 30px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  box-sizing: border-box;
}

.logo {
  display: inline-block;
}

.show-nav {
  position: absolute;
  right: 20px;
  top: 20px;
  display: none;
  text-transform: uppercase;
  font-family: "BarlowCondensed";
  color: #fff;
  background-color: transparent;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

@media (max-width: 768px) {
  .visually-hidden-mobile {
    display: none;
  }
  .main-header {
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    -o-flex-direction: column;
    flex-direction: column;
    align-items: center;
  }
  .show-nav {
    display: block;
  }
}

.main-nav {
  margin-left: 40px;
}

.main-nav__list {
  margin: 0;
  padding: 0;
}

.main-nav__item {
  display: inline-block;
  padding: 5px;
  margin-right: 15px;
  border: 1px solid transparent;
  border-radius: 4px;
}

.main-nav__item:hover {
  background-color: #092F3B;
  border-left: 1px solid #07252F;
  border-bottom: 1px solid #426471;
  border-top: 1px solid #07252F;
  border-right: 1px solid #426471;
}

.main-nav__link {
  text-transform: uppercase;
  font-family: "BarlowCondensed";
  font-size: 18px;
  color: #fff;
  text-decoration: none;
}

.main-nav__link:last-child {
  margin-right: 0;
}

@media (max-width: 768px) {
  .main-nav {
    width: 100%;
    margin: 0;
    margin-top: 20px;
  }
  .main-nav__item {
    margin: 0;
    display: block;
    text-align: center;
  }
}

.info-menu {
  margin: 0;
  padding: 0;
  margin-top: -10px;
}

.info-menu__item {
  display: inline-block;
  margin-right: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 5px;
}

.info-menu__item:hover {
  background-color: #113344;
  border-left: 1px solid #07252F;
  border-bottom: 1 px solid #426471;
  border-top: 1px solid #07252F;
  border-right: 1 px solid #426471;
}

.info-menu__link {
  text-transform: uppercase;
  font-family: "BarlowCondensed";
  font-size: 14px;
  color: #A6A6A6;
  text-decoration: none;
  line-height: 22px;
}

.info-menu__link:last-child {
  margin-right: 0;
}

@media (max-width: 768px) {
  .info-menu__item {
    display: block;
    width: 100%;
    text-align: center;
    padding: 0;
  }
}

.title {
  display: block;
  background-color: #CACACA;
  min-height: 35px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font-size: 12px;
  color: #757575;
}

.title__main {
  text-transform: uppercase;
  font-size: 18px;
  font-family: "BarlowCondensed";
  color: #fff;
  background-color: #AE0002;
  min-height: 35px;
  line-height: 35px;
  padding: 0 37px 0 15px;
  position: relative;
  margin-right: 35px;
}

.title__main:after {
  content: "";
  position: absolute;
  border: 17px solid transparent;
  border-left: 17px solid #AE0002;
  z-index: 1;
  right: -34px;
}

.title--new-post {
  margin-bottom: 35px;
  margin-top: 35px;
}

@media (max-width: 768px) {
  .title {
    display: block;
    width: 100%;
    text-align: center;
    padding: 5px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  .title__main {
    display: block;
    width: 100%;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin-bottom: 5px;
  }
  .title__main:after {
    border: none;
  }
}

.news {
  width: 725px;
  margin: 0 auto;
}

.news__main {
  position: relative;
}

.news__main-description {
  max-width: 340px;
  padding: 20px;
  background-color: #1E1E1E;
  font-size: 12px;
  line-height: 18px;
  color: #fff;
  position: absolute;
  left: 40px;
  bottom: 40px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  .news {
    width: 320px;
  }
  .news__main-description {
    position: relative;
    max-width: 320px;
    left: 0;
    bottom: 0;
    margin-bottom: 5px;
  }
}

.videos {
  padding-left: 30px;
}

.videos__title {
  font-family: "BarlowCondensed";
  background-color: #AE0002;
  min-height: 35px;
  padding-left: 15px;
  color: #fff;
  font-size: 18px;
  line-height: 35px;
  text-transform: uppercase;
  border-radius: 3px;
  margin: 0;
  font-weight: 400;
  margin-bottom: 5px;
}

.sm-videos-wrapper {
  display: grid;
  grid-template-rows: auto auto auto;
  grid-row-gap: 15px;
}

@media (max-width: 960px) {
  .videos {
    padding: 0;
    width: 725px;
    margin: 0 auto;
    margin-top: 35px;
  }
  .sm-videos-wrapper {
    margin: 0 auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    margin-top: 35px;
  }
}

@media (max-width: 768px) {
  .videos {
    width: 320px;
    margin: 0 auto;
    margin-top: 30px;
  }
}

.sm-new {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
  -o-flex-direction: column;
  flex-direction: column;
  -ms-align-items: center;
  align-items: center;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 15px;
  background-color: #fff;
  border-radius: 4px;
  border-right: 1px solid #D4D4D4;
  border-bottom: 1px solid #D4D4D4;
  box-shadow: 1px 1px 0px 0px #e5e5e5;
}

.sm-new__img {
  margin-bottom: 15px;
}

.sm-new__link {
  color: #042B38;
  font-size: 12px;
  line-height: 18px;
}

.sm-new__link:hover {
  text-decoration: none;
  color: #AE0002;
}

.sm-new__description {
  color: #757575;
  font-size: 11px;
  line-height: 18px;
  font-style: italic;
}

.sm-new__show {
  width: 35px;
  height: 35px;
  border-radius: 2px;
  background: url(../img/play.png) 10px 8px, linear-gradient(to top, #1B1B1B, #2A2A2A);
  background-repeat: no-repeat;
  position: absolute;
  left: 10px;
  top: 125px;
}

.sm-new__show:hover {
  background: url(../img/play.png) 10px 8px, #AE0002;
  background-repeat: no-repeat;
}

.sm-new--video {
  background-color: transparent;
  padding: 0;
  border: none;
  box-shadow: none;
  position: relative;
}

.sm-news-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-column-gap: 10px;
}

.comments {
  -ms-align-self: flex-end;
  align-self: flex-end;
  font-size: 11px;
  color: #757575;
  font-style: italic;
  position: relative;
  text-decoration: none;
  margin-top: auto;
  padding-left: 20px;
}

.comments:hover {
  color: #AE0002;
}

.comments:before {
  content: "";
  height: 15px;
  width: 20px;
  background-image: url(../img/comment.png);
  background-repeat: no-repeat;
  position: absolute;
  left: 0px;
}

@media (max-width: 960px) {
  .sm-new__show {
    left: 0;
    top: 0;
  }
}

@media (max-width: 768px) {
  .sm-news-wrapper {
    grid-template-columns: 1fr;
    margin-bottom: 0;
  }
  .sm-new {
    margin-bottom: 15px;
  }
  .sm-new:last-child {
    margin-bottom: 0;
  }
}

.new-post {
  display: inline-block;
}

.new-post__img {
  float: left;
  margin-right: 25px;
}

.new-post__text {
  font-size: 11px;
  color: #757575;
  line-height: 18px;
  margin-bottom: 20px;
}

.new-post__tags {
  font-size: 12px;
  color: #757575;
  margin-left: 20px;
  margin-right: 10px;
}

.new-post__share {
  margin-left: 25px;
  font-size: 12px;
  color: #757575;
}

.new-post .comments {
  display: inline-block;
}

.tags-list {
  margin: 0;
  padding: 0;
  display: inline-block;
}

.tags-list__item {
  display: inline-block;
}

.tags-list__link {
  color: #2B5E5E;
  font-size: 12px;
  font-style: italic;
}

.tags-list__link:hover {
  text-decoration: none;
}

.social {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: middle;
}

.social:hover {
  transform: scale(0.8);
}

.facebook {
  background-image: url(../img/facebook.png);
  margin-right: 2px;
}

.twitter {
  background-image: url(../img/twitter.png);
}

@media (max-width: 768px) {
  .new-post {
    position: relative;
  }
  .new-post__tags {
    margin-left: 10px;
  }
  .new-post .comments {
    position: absolute;
    right: 0px;
    bottom: 0px;
  }
  .new-post__share {
    margin-left: 10px;
    margin-top: 10px;
    display: inline-block;
  }
}

.email {
  margin-top: 35px;
}

.email__field {
  height: 30px;
  width: 100%;
  background-color: #F6F6F6;
  position: relative;
  margin-top: 13px;
  border-radius: 4px;
  border-right: 1px solid #D4D4D4;
  border-bottom: 1px solid #D4D4D4;
  box-shadow: 1px 1px 0px 0px #e5e5e5;
}

.email__input {
  width: 100%;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  height: 30px;
  font-style: italic;
  font-size: 12px;
  padding-left: 15px;
  box-sizing: border-box;
}

.email__send {
  position: absolute;
  right: 0;
  top: 7px;
  width: 35px;
  margin: 0;
  padding: 0;
  border: none;
  height: 16px;
  border-left: 1px solid #CACACA;
  vertical-align: middle;
  background-color: transparent;
  background-image: url(../img/envelope.png);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.email__social {
  display: blocks;
  margin-top: 10px;
}

.bgSocial {
  width: 31px;
  height: 31px;
  display: inline-block;
}

.bgSocial:hover {
  transform: scale(0.8);
}

.bgFacebook {
  background-image: url(../img/bgFb.png);
}

.bgTwitter {
  background-image: url(../img/bgTw.png);
}

.bgRss {
  background-image: url(../img/bgRss.png);
}

@media (max-width: 960px) {
  .email {
    margin-top: 0;
  }
}
